import {css} from 'react-emotion';
import styled from '../../themes/styled';
import {hideVisually} from '../../styles';

const base = () => css`
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  padding: 0;
  user-select: none;

  > input {
    ${hideVisually()};
  }
`;

const disabled = ({disabled: isDisabled}) =>
  isDisabled &&
  css`
    cursor: not-allowed;
    opacity: 0.5;
    transition: opacity 0.25s;
  `;

const track = ({theme}) => css`
  position: relative;
  display: inline-block;
  width: 42px;
  height: 22px;
  border: 1px solid ${theme.colors.grays[500]};
  border-radius: 16px;
  background-color: ${theme.colors.page.background};
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12);
  transition-property: background-color, border-color;
  transition-duration: 0.2s;

  input:focus + &,
  input:active + & {
    ::after {
      box-shadow: 0px 0px 2px 3px ${theme.colors.interactive.focus.outline};
    }
  }
  ::after {
    content: ' ';
    display: inline-block;
    position: absolute;
    top: 2px;
    left: 2px;
    width: 16px;
    height: 16px;
    border: 1px solid ${theme.colors.grays[500]};
    border-radius: 50%;
    background-color: white;
    transition-property: border-color, left;
    transition-duration: 0.2s;
    box-sizing: border-box;
  }
  ::before {
    content: ' ';
    display: inline-block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: 80% center;
    background-repeat: no-repeat;
    background-image: url(data:image/svg+xml;base64,);
  }
  input:checked + & {
    border-color: ${theme.colors.interactive.checked.border};
    background-color: ${theme.colors.interactive.checked.background};

    ::after {
      border-color: ${theme.colors.interactive.checked.border};
      left: 50%;
    }

    ::before {
      background-position: 20% center;
      background-image: url(data:image/svg+xml;base64,);
    }
  }
`;

export const Container = styled.div<any>(base, disabled);
export const Track = styled.div<any>(track);
